.portrait-wrapper {
  display: contents;
}

.portrait {
  position: relative;
  display: inline-flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 4px;
  margin: 10px;
  width: 205px;
  height: 420px;
  max-width: calc(100% - 20px - 20px);
  padding: 12px;
  border: 1px solid var(--border);
  border-radius: var(--rounded);
  background: var(--surface);
  box-shadow: var(--shadow);
  overflow: hidden;
  text-decoration: none;
  transition: border-color var(--transition), background var(--transition),
    transform var(--transition), box-shadow var(--transition);
}

.portrait:hover {
  transform: translateY(-2px);
  border-color: var(--primary-soft);
  background: var(--surface-alt);
  box-shadow: var(--shadow-strong);
}

.portrait[data-style="small"] {
  width: 138px;
  height: 300px;
  padding: 10px;
}

.portrait[data-style="tiny"] {
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  gap: 12px;
  height: auto;
  width: unset;
  overflow: visible;
  padding: 10px;
  text-align: left;
}

.portrait .icon {
  position: absolute;
  right: 10px;
  top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 28px;
  aspect-ratio: 1 / 1;
  border-radius: 999px;
  background: var(--surface-frost);
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
  color: var(--primary);
  transform: translateY(0);
  transition: transform var(--transition), box-shadow var(--transition);
}

.portrait[data-style="small"] .icon {
  right: 8px;
  top: 8px;
}

.portrait[data-style="tiny"] .icon {
  display: none;
}

.portrait-image {
  width: 100%;
  margin-bottom: 10px;
  aspect-ratio: 4 / 5;
  border-radius: var(--rounded-sm);
  object-fit: cover;
  box-shadow: var(--shadow);
  cursor: zoom-in;
  transition: transform var(--transition);
}

.portrait:hover .portrait-image {
  transform: scale(1.018);
}

.portrait:hover .icon {
  transform: translateY(-1px);
  box-shadow: var(--shadow-strong);
}

.portrait[data-style="tiny"] .portrait-image {
  width: 54px;
  aspect-ratio: 1 / 1;
  margin: 0;
  box-shadow: var(--shadow);
}

.portrait[data-style="profile"] {
  width: min(100%, 380px);
  height: auto;
  overflow: visible;
  padding: 14px;
}

.portrait[data-style="profile"] .portrait-image {
  aspect-ratio: auto;
  height: auto;
  object-fit: contain;
}

.portrait[data-style="profile"] .portrait-name {
  font-size: 1.18rem;
}

.portrait-name {
  width: 100%;
  font-family: var(--heading);
  font-size: 1rem;
  font-weight: var(--bold);
  color: var(--text);
  line-height: 1.25;
}

.portrait-description,
.portrait-affiliation {
  width: 100%;
  overflow: hidden;
  color: var(--gray);
  font-size: 0.9rem;
  line-height: 1.4;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.portrait[data-style="tiny"] {
  .portrait-description,
  .portrait-affiliation {
    display: none;
  }
}

.portrait[data-no-image="true"] {
  align-items: center;
  justify-content: center;
  text-align: center;

  .icon {
    display: none;
  }

  .portrait-name {
    font-size: 1rem;
  }
}

.portrait[data-style="small"][data-no-image="true"] {
  width: 160px;
  height: 250px;
  padding: 12px 10px;
  display: grid;
  grid-template-rows: 4.5em 3em 3em;
  align-content: center;
  justify-items: center;
  gap: 6px;

  .portrait-name {
    min-height: 0;
    max-height: none;
    width: 100%;
    margin: 0;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: center;
    font-size: 0.96rem;
    line-height: 1.25;
    overflow-wrap: break-word;
  }

  .portrait-description,
  .portrait-affiliation {
    min-height: 0;
    width: 100%;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-align: center;
    font-size: 0.84rem;
    line-height: 1.35;
    -webkit-line-clamp: 2;
  }
}

.alumni-row {
  display: flex;
  flex-wrap: nowrap;
  align-items: stretch;
  overflow-x: auto;
  overflow-y: hidden;
  padding-bottom: 6px;
  -webkit-overflow-scrolling: touch;
}

.alumni-row > .portrait {
  flex: 0 0 auto;
}

@media (max-width: 720px) {
  .portrait {
    width: 100%;
    max-width: 100%;
    height: auto;
    min-height: 0;
    margin: 0;
    padding: 12px;
  }

  .portrait[data-style="small"] {
    width: 100%;
    height: auto;
    min-height: 0;
  }

  .portrait-image {
    max-height: min(62vw, 320px);
    margin-bottom: 8px;
  }

  .portrait[data-style="small"] .portrait-image {
    max-height: min(58vw, 260px);
  }

  .portrait[data-style="small"][data-no-image="true"] {
    width: 100%;
    height: auto;
    min-height: 0;
    grid-template-rows: auto;
    justify-items: stretch;
    gap: 8px;
  }

  .alumni-row > .portrait {
    flex: 0 0 min(82vw, 280px);
    width: min(82vw, 280px);
    margin-right: 10px;
  }
}
